import { createStore } from "redux";

const Reducer = (state = {name:"user_name", list:[],item:"", completed_list:[]}, action)=>{
    switch(action.type){
        case "item":
            // console.log(state.item);
            return {...state, item:action.msg};
        case "add":
            if(state.item!="" && state.list.indexOf(state.item)==-1){
                console.log("present");
                return {...state,list:state.list.concat(state.item)};
            }
            // console.log(state.list);
            return state;
        case "completed":
            let temp = state.list;
            let temp2=state.list[action.index];
            temp.splice(action.index,1);
            return {...state, completed_list:state.completed_list.concat(temp2), list:temp};
        case "delete":
            let del = state.completed_list;
            del.splice(action.index, 1);
            return {...state, completed_list:[...del]};
    }
    return state;
}

const store = createStore(Reducer);
export default store;