import React from 'react'
import { useDispatch, useSelector } from 'react-redux';
import styled from 'styled-components';
import Btn from './btn';
import axios from 'axios';

const Completed_list = () => {
    const completed_list = useSelector(state => state.completed_list);
    const dispatch = useDispatch();
    const del = (e,i)=>{
        console.log(i);
        dispatch({type:"delete", index:i});
    }
    return (
        <Flexcontainer>
        <Container>
            <h1>Completed Tasks</h1>
            {completed_list!=0&&completed_list.map((v,i)=>{
                return <Listitem key={i}>{v}<Container2 onClick={(e)=>del(e,i)}><Btn btn_name="delete" color="1"></Btn></Container2></Listitem>;
            })}
            {completed_list==0&&<p>such empty.</p>}
        </Container>
        </Flexcontainer>
    )
}
// onClick={(e)=>completed(e,i)}
const Listitem = styled.h2`
    border:1px solid hsl(0deg 0% 0% / 0.1);
    margin: 2rem 1rem;
    border-radius:5px;
    padding:1rem 2rem 0.3rem;
    text-align:start;
    background-color:rgba(47, 43, 40, 0.144);
    word-wrap: break-word;
    box-shadow:
      0 1px 1px hsl(0deg 0% 0% / 0.1),
      0 2px 2px hsl(0deg 0% 0% / 0.1),
      0 4px 4px hsl(0deg 0% 0% / 0.1),
      0 8px 8px hsl(0deg 0% 0% / 0.1),
      0 16px 16px hsl(0deg 0% 0% / 0.1);
      
      color: white;
      text-shadow:0px 1px 4px black;
      font-size: 22px;
      font-weight: normal;
      font-family: sans-serif;
`;
const Container =styled.div`
text-align:center;
margin:1rem;
padding:1rem;
background-color:#bac1ecca;
border-radius:5px;
`;
const Container2 =styled.div`
    display:flex;
    // flex-direction:row-reverse; 
    flex-direction:row-reverse;
   
`;
const Flexcontainer=styled.div`
width:40%;
    @media (max-width:800px){
        width:80%;
    }
`;
export default Completed_list
