import axios from 'axios';
import React, { useEffect, useState } from 'react'
import styled from 'styled-components';


const Axioscmp = () => {
    let [quote, setQuote] = useState("this is a quote");
    const [author, setAuthor] = useState("-author");
    const baseurl = "https://type.fit/api/quotes";
    const fetchFact = async ()=>{
        const res = await axios.get(`${baseurl}`,{ headers: { "Accept": "application/json" } });
        console.log(res);
        console.log(res.data[Math.floor(Math.random()*1640)]);
        setQuote(res.data[Math.floor(Math.random()*1640)].text);
        setAuthor(res.data[Math.floor(Math.random()*1640)].author);
        // setFacts(facts.concat(res.data.joke));
    }
    useEffect(() => {
        fetchFact();
        return () => {}
    }, [])
    return (
        <div>
            <Container onClick={()=>fetchFact()}>
            <b>{quote}</b>
            <Author>-{author}</Author>
            </Container>
        </div>
    )
}
const Container = styled.h2`
    border:1px solid hsl(0deg 0% 0% / 0.1);
    background-color:#E6E6FA;
    // background-color:#E1E2E2;
    margin: 2rem 2rem;
    text-align:center;
    border-radius:5px;
    padding:1rem 2rem;
    word-wrap: break-word;
    box-shadow:
      0 1px 1px hsl(0deg 0% 0% / 0.1),
      0 2px 2px hsl(0deg 0% 0% / 0.1),
      0 4px 4px hsl(0deg 0% 0% / 0.1),
      0 8px 8px hsl(0deg 0% 0% / 0.1),
      0 16px 16px hsl(0deg 0% 0% / 0.1);

      color: black;
    //   text-shadow:0px 1px 4px black;
      font-size: 22px;
      font-weight: normal;
      font-family: sans-serif;
      text-transform:capitalize;
      cursor: pointer;
`;

const Author = styled.div`
    text-align:right;
    padding-top:0.5rem;
`;
export default Axioscmp
