import React, { useState } from 'react'
import { useDispatch } from 'react-redux'
import styled from 'styled-components'

const Textin = () => {
    const [msg, setMsg] = useState("this is a message");
    const dispatch = useDispatch();
    const changed =(e)=>{
        setMsg(e.target.value);
        dispatch({type:"item", msg:e.target.value});
    }
    return (
        <Input onChange={(e)=>changed(e)}></Input>
    )
}

const Input = styled.textarea`
width:50%;
box-sizing:border-box;
height:100px;
font-size:1rem;
border:1px solid hsl(0deg 0% 0% / 0.1);
padding:1rem 1rem;
color black;
background-color:rgba(47, 43, 40, 0.144);
box-shadow:
      0 1px 1px hsl(0deg 0% 0% / 0.1),
      0 2px 2px hsl(0deg 0% 0% / 0.1),
      0 4px 4px hsl(0deg 0% 0% / 0.1),
      0 8px 8px hsl(0deg 0% 0% / 0.1),
      0 16px 16px hsl(0deg 0% 0% / 0.1);
margin:1rem;
&:focus{
    color:white;
    background-color:rgba(47, 43, 40, 0.678);
    border:none;
}
@media(max-width:800px){
    width:80%;
    height:80px;
}
`;
export default Textin
