import React from 'react'
import styled from 'styled-components'

const Info = () => {
    return (
        <Container>
            <p> 
            This website is created using react {<i class="fab fa-react"></i>} by   {<i class="fab fa-korvue"></i>} {<i class="fab fa-atlassian"></i>} {<i class="fab fa-ravelry"></i>} {<i class="fab fa-tumblr"></i>} {<i class="fas fa-heading"></i>}
            {<i class="fas fa-italic"></i>} {<i class="fab fa-kickstarter-k"></i>} </p>
        </Container>
    )
}
const Container = styled.h2`
text-align:center;
color:white;
text-align:center;
// height:3rem;
// margin-bottom:0px;
margin:0px;
padding:1px 0px;
background-color:#393f4d;
`;

export default Info
