import { getByDisplayValue } from '@testing-library/dom';
import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import styled from 'styled-components';
import Btn from './btn';

const Todo_list = () => {
    const dispatch = useDispatch();
    let temp=useSelector(state => state.list);
    const completed_list = useSelector(state => state.completed_list);

    const completed = (e,index)=>{
        console.log(index);
        dispatch({type:"completed", index:index});
        console.log(completed_list)

    }
    return (
        
        <Flexcontainer>
            <Container>
            <h1>Todo Tasks</h1>
            {(temp!=0)&&temp.map((v,i)=>{
                return <Listitem key={i}>{v}<Container2 onClick={(e)=>completed(e,i)}><Btn btn_name="completed" color="2"></Btn></Container2></Listitem>;
            })}
            {(temp==0)&&<p>such empty.</p>}
        </Container>
        </Flexcontainer>
    )
}

const Listitem = styled.h2`
    border:1px solid hsl(0deg 0% 0% / 0.1);
    background-color:rgba(47, 43, 40,0.144);
    // background-color:#E1E2E2;
    margin: 2rem 1rem;
    // color:white;
    text-align:start;
    border-radius:5px;
    padding:1rem 2rem 0.3rem;
    word-wrap: break-word;
    box-shadow:
      0 1px 1px hsl(0deg 0% 0% / 0.1),
      0 2px 2px hsl(0deg 0% 0% / 0.1),
      0 4px 4px hsl(0deg 0% 0% / 0.1),
      0 8px 8px hsl(0deg 0% 0% / 0.1),
      0 16px 16px hsl(0deg 0% 0% / 0.1);

      color: white;
      text-shadow:0px 1px 4px black;
      font-size: 22px;
      font-weight: normal;
      font-family: sans-serif;
`;

const Container =styled.div`
text-align:center;
background-color:#bac1ecca;
border-radius:5px;
margin:1rem;
padding:1rem;
`;
const Container2 =styled.div`
    // background-color:red;
    display:flex;
    flex-direction:row-reverse;
`;
const Flexcontainer=styled.div`
width:40%;


@media (max-width:800px){
    width:80%;
}
`;
export default Todo_list
