import React, { useState } from 'react'
import styled from 'styled-components';

const Btn = (props) => {
    const [name, setName] = useState(props.btn_name);
    const [colors, setColors] = useState([["#7395AE","#557A95","#B1A296"],["#DE354C","#932432","#3C1874"],["#4DA8DA","#007CC7","#12232E"]]);
    let index = 0;
    if(props.color){
        index=props.color;
    }
    // setName(props.btn_name);
    return (
        <div>
            <Button color={colors} index={index}>{name}</Button>  
        </div>
    )
}
const Button = styled.button`
cursor: pointer;
color:white;
padding:0.5rem 1rem;
margin:1rem;
border:none;
border-radius:5px;
font-size:1rem;
// background-color: rgb(102, 156, 255);
background-color: ${props => props.color[props.index][0]};

box-shadow:
      0 1px 1px hsl(0deg 0% 0% / 0.1),
      0 2px 2px hsl(0deg 0% 0% / 0.1),
      0 4px 4px hsl(0deg 0% 0% / 0.1),
      0 8px 8px hsl(0deg 0% 0% / 0.1),
      0 16px 16px hsl(0deg 0% 0% / 0.1);

&:hover{
    background-color: ${props => props.color[props.index][1]};
    box-shadow: 
    0 1px 1px rgba(0, 0, 0, 0.1),
    0 2px 2px rgba(0, 0, 0, 0.1),
    0 4px 4px rgba(0, 0, 0, 0.122),
    0 8px 8px rgba(0, 0, 0, 0.122),
    0 16px 16px rgba(0, 0, 0, 0.122);
&:active{
    background-color: ${props => props.color[props.index][2]};
}
}
`;


export default Btn;